@import "../../variables";

.UserScore {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 4rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 400;
    background-image: linear-gradient(to right, $color-primary, $color-tertiary-dark);
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
}